import React, { Component } from 'react';
import Layout from '../components/layout';

export default class MacInstallPage extends Component {
  render() {
    const { location } = this.props;
    const scriptUrl = `${location.origin}/mac.sh`;
    return (
      <Layout>
        <p>
          Run the following command in your terminal to setup a new Mac
          interactively.
        </p>
        <p
          style={{
            borderRadius: '5px',
            border: '1px solid #2E333E',
            padding: '5px',
          }}
        >
          {`/bin/bash -c "$(curl -fsSL ${scriptUrl})"`}
        </p>
      </Layout>
    );
  }
}
